<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isFundsTransferAll')">{{ $t('MODIFY') }}</vs-button>
    </div>
    <div class="nv-row hover">
      <div class="nv-col sm:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('ID') }}:</span>
          <span>{{ postingRule.ruleId | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('APP') }}:</span>
          <span>{{ postingRule.appName }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TRANSFER_TYPE') }}:</span>
          <span>{{ postingRule.transferTypeName }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('POSTING_TYPE') }}:</span>
          <span>{{ postingRule.postingTypeDescription }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CURRENCY_TYPE') }}:</span>
          <span>{{ postingRule.currencyTypeDescription }}</span>
        </p>
        <p class="info-row" v-if="postingRule.currencyTypeEnumId === 'FT_CCY_EXPLICIT'">
          <span>{{ $t('TRANSFER_CURRENCY') }}:</span>
          <span>{{ postingRule.transferCurrencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="postingRule.currencyTypeEnumId === 'FT_CCY_EXPLICIT'">
          <span>{{ $t('DEBIT_CURRENCY') }}:</span>
          <span>{{ postingRule.debitCurrencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TIME') }}:</span>
          <span>{{ postingRule.fromTime | placeholder('-') }} - {{ postingRule.toTime | placeholder('-')  }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('AMOUNT') }}:</span>
          <span>{{ postingRule.fromAmount | currency('', 0) | placeholder('*') }} - {{ postingRule.toAmount | currency('', 0) | placeholder('*') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('ROLLOVER') }}:</span>
          <span>{{ postingRule.rollover | placeholder('-')  }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CREATION_DATE') }}:</span>
          <span>{{ postingRule.creationDate | formatUnixTimestamp }}</span>
        </p>
      </div>
      <div class="nv-col sm:w-1/2 w-full">
        <p class="info-row" v-if="postingRule.description">
          <span>{{ $t('DESCRIPTION') }}:</span>
          <span>{{ postingRule.description | placeholder('-')  }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('PARTY_CLASS') }}:</span>
          <span>{{ postingRule.partyClassificationDescription | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="postingRule.externalPartyId">
          <span>{{ $t('EXTERNAL_PARTY_ID') }}:</span>
          <span>{{ postingRule.externalPartyId | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('FROM_DATE') }}:</span>
          <span>{{ postingRule.fromDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TO_DATE') }}:</span>
          <span>{{ postingRule.toDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('POSITION') }}:</span>
          <span>{{ postingRule.position | placeholder('-')  }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('DISABLED') }}:</span>
          <span>{{ postingRule.disabled| prettyBool  }}</span>
        </p>
      </div>
    </div>
  </nv-card>
</template>

<script>
import PostingRuleService from '@/services/ft/PostingRuleService'

export default {
  name: 'posting-rule-view',
  data: function () {
    return {
      postingRule: {},
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      PostingRuleService.postingRules({
        ruleId: this.$route.params.id
      }).then(function (response) {
        self.postingRule = response.data.postingRuleList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyPostingRule', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
